.container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f4f4f9;
  }
  
  /* Tarjeta principal */
  .card {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 500px;
    text-align: center;
  }
  
  /* Input y bot√≥n */
  .input-container {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
  }
  
  .input-field {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    transition: border 0.3s ease;
  }
  
  .input-field:focus {
    border: 1px solid #007bff;
  }
  
  .search-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .search-button:hover {
    background-color: #0056b3;
  }
  
  /* Mensajes de error */
  .error-message {
    color: red;
    font-size: 14px;
    margin-bottom: 10px;
  }
  
  /* Tabla de empleados */
  .table-container {
    overflow-x: auto;
  }
  
  .employee-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .employee-table th, 
  .employee-table td {
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  .employee-table th {
    background-color: #007bff;
    color: white;
  }
  
  .employee-table tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .employee-table tr:hover {
    background-color: #f1f1f1;
  }